import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader, Cell } from '@vkontakte/vkui';

const Home = ({ id, go, dataUser, phoneTokenUser }) => (
	<Panel id={id}>
		<PanelHeader>User Data</PanelHeader>
		
		{dataUser &&
		<Group title="Пользователь">
			<ListItem
				before={dataUser.photo_200 ? <Avatar src={dataUser.photo_200}/> : null}
				description={dataUser.city && dataUser.city.title ? dataUser.city.title : ''}
			>
				{`${dataUser.first_name} ${dataUser.last_name}`}
			</ListItem>
		</Group>}
		
		{phoneTokenUser &&
		<Group title="Номер телефона">
			<Cell
			>
				+{`${phoneTokenUser.phone_number}`}
			</Cell>
		</Group>}
		
		
        {dataUser &&
		<Group title="Панель">
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="secret">
					Открыть
				</Button>
			</Div>
		</Group>}
		</Panel>
);		
		
Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	phoneTokenUser: PropTypes.shape({
	    phone_number: PropTypes.string,
		access_token: PropTypes.string,
	dataUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
  }),
};


export default Home;
