import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Secret from './panels/Secret';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			dataUser: null,
			phoneUser: null,
			tokenUser: null
		};
	}


	
	componentDidMount() {
		connect.subscribe((b) => {
			switch (b.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ dataUser: b.detail.data });
					break;
				case 'VKWebAppGetPhoneNumberResult':
					this.setState({ phoneTokenUser: b.detail.data });
					break;
				default:
					console.log(b.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppGetPhoneNumber", {});
	}
	

	go = (b) => {
		this.setState({ activePanel: b.currentTarget.dataset.to })
	};
	
	
	

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" dataUser={this.state.dataUser} phoneTokenUser={this.state.phoneTokenUser} go={this.go} />
				<Secret id="secret" go={this.go} />
			</View>
		);
	}
}

export default App;
